import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '@/views/Index'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home,
    meta: {
      forAuth: false, 
    }
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router